.todo-list {
  text-align: center;
}
.wrap-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 900px;
  margin: 0 auto;
  line-height: 30px;

  ul {
    display: flex;
    flex-direction: column;
    margin: 10px 0;
    font-size: 18px;
    li {
      display: flex;
      //padding-left: 45px;
      align-items: center;
      border-bottom: 2px solid $accent__color;
      padding-bottom: 2px;
      &:hover {
        border-bottom: 2px solid red;
      }
      .btn-del {
        padding: 5px 14px;
        margin-left: auto;
        background: red;
        border: 2px solid red;
        &:hover {
          background: $primary-color;
          color: red;
        }
      }
    }
    .new-item {
      flex: 1;
      border: 2px solid #ccc;
      text-indent: 20px;
    }
    input[type=checkbox] {
      margin: 0 10px;
    }
  }

}

.header-list {
  display: flex;
  margin: 20px 0;
  .add-task {
    text-indent: 20px;
    border-radius: 5px 0 0 5px;
    border: 2px solid $accent__color;
    flex: 1;
    &:active,
    &:focus {
      border-color: $color__interactive;
    }
  }
  .btn-add {
    min-width: 88px;
    background: $accent__color;
    border: 2px solid $accent__color;
    &:hover {
      background: $primary-color;
      color: $accent__color;
    }
  }
}

.btn {
  padding: 5px 10px;
  border-radius: 0 5px 5px 0;
  color: $primary-color;
  font-size: 20px;
}

.count {
  display: inline-block;
  width: 45px;
  height: 45px;
  border: 2px solid $accent__color;
  border-radius: 50%;
  line-height: 43px;
  font-size: 18px;
}







