.top-menu {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.menu-item {
  margin: 5px 10px;
  a {
    display: block;
    padding: 5px 10px;
    border: 2px solid $accent__color;
    background: $accent__color;
    color: $primary-color;
    border-radius: 10px;
    &:hover {
      background: $color__interactive;
      border-color: $color__interactive;
    }
  }
}

.slideUp {
    height:0;
}