.box-list {
  margin: 25px 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  text-align: center;
  .box-item {
    color: $primary-color;
    margin: 5px;
    border: 2px solid $secondary-color;
    background: $accent__color;
    padding: 25px;
  }
}

.circle {
  position: absolute;
  top: 1px;
  width: 50px;
  height: 50px;
  color: transparent;
  border: 3px solid $color__interactive;
  border-radius: 50%;

}